import React, { useState, useRef } from 'react'
import { IArtist } from '../../objects/artist'
import { deleteCast, ICast, updateCast } from '../../objects/cast'
import { ICastType } from '../../objects/castType'
import CastTypeSelect from './CastTypeSelect'
import "./castStyle.css";
import { useAsyncBinder } from '../../hooks'

interface IEditCastRow{
    cast: ICast;
    castTypes: ICastType[];
    onDelete: ()=>void;
    onUpdate: (updated: ICast)=>void;
}

export const EditCastRow: React.FC<IEditCastRow> = ({cast, castTypes, onDelete, onUpdate}: IEditCastRow) => {
    const [mainClass, setMainClass] = useState("edit-cast-row");
    const [getCast, setCast, castBinder] = useAsyncBinder(cast);
    async function deleteRow(){
        if(await deleteCast(cast.id)){
            onDelete();
        } else {
            alert("Server error");
        }
    }

    async function saveChanges(){
        if(await updateCast(getCast.current)){
            onUpdate(getCast.current); 
            saved();
        } else {
            alert("Server error: could not save your work.");
        }
    }

    const setCastTypeId = (newTypeId: string) =>{
        setCast({...getCast.current, castTypeId: parseInt(newTypeId)});
    }

    const setUnsaved = ()=>{
        if(!mainClass.includes("unsaved")){
            setMainClass("edit-cast-row unsaved");
        }
    }

    const saved = ()=>{
        if(mainClass.includes("unsaved")){
            setMainClass(mainClass.replace(" unsaved", ""));
        }
    }

    return (
        <div className={mainClass}>
            <img src={`/rewer/uploads/portraits/${getCast.current.artistId}.jpg`} alt={cast.artistName} />
            <p>{getCast.current.artistName}</p>
            <input name="name" value={getCast.current.name} onChange={(e)=>{ castBinder(e); setUnsaved(); } } />
            <CastTypeSelect typeList={castTypes} initialValue={getCast.current.castTypeId} updateValue={(newTypeId: string)=>{ setCastTypeId(newTypeId); setUnsaved();} } />
            <button onClick={saveChanges} >Update</button>
            <button onClick={deleteRow}>Delete</button>
        </div>
    )
}

export default EditCastRow;
