import * as caller from "./caller";
export interface IFollow{
    id: number;
    follower: string;
    followed: string;
}

export async function insertFollow(follow: IFollow): Promise<number>{
    const response = await caller.POSTAsynch("/follow/insertfollow.php", follow);
    return caller.parseId(response);
}

export async function deleteFollow(follow: IFollow){
    const response = await caller.POSTAsynch("/follow/deletefollow.php", follow);
    return caller.isResponse200(response);
    
}