import React, { useState, useRef, useEffect } from 'react'
import { useBinder } from '../../hooks';
import { logOut } from '../../objects/authUser';
import { selectDetailedUser, updateUserInfo, uploadAvatar } from '../../objects/user';
import ChangePassword from './ChangePassword';
import "./profileStyle.css";

export default function MyProfile() {
    const [myProfile, setMyProfile, bindMyProfile] = useBinder({ userName: localStorage.getItem("userName"), nickName: localStorage.getItem("nickName"), permission: 1, about: "", followerCount: 0 })
    const [nickNameMsg, setNickNameMsg] = useState("");
    const [uploadMsg, setUploadMsg] = useState("");
    const [avatarSource, setAvatarSource] = useState(`/rewer/uploads/avatars/${localStorage.getItem("userName")}.jpg`);
    const imageFile = useRef<HTMLInputElement>();

    useEffect(()=>{
        getMyInfo();
    }, []);

    async function logoutCommand(){
        await logOut();
        window.location.reload();
    }

    async function updateUserInfoCommand(){
        if(await updateUserInfo(myProfile)){
            setNickNameMsg("Your profile has been updated");
        } else {
            setNickNameMsg("Request failed.");
        } 
    }

    async function getMyInfo(){
        let profile = await selectDetailedUser(localStorage.getItem("userName"));
        if(profile){
            setMyProfile(profile);
        } else {
            //TODO 404
        }
    }

    function handleKeyDown(e){
        if(e.key === 'Enter'){
            updateUserInfoCommand();
        }
    }

    async function uploadImageFileCommand(){
        const formData = new FormData();
        formData.append("imageFile", imageFile.current.files[0], `${localStorage.getItem("userName")}.jpg`);
        if(await uploadAvatar(formData)){
            setUploadMsg("Avatar updated.");
            setAvatarSource(`/rewer/uploads/avatars/${localStorage.getItem("userName")}.jpg?${new Date().getTime()}`);
        } else {
            setUploadMsg("Process failed. Make sure file's size is not bigger than 10MB.");
        }
    }
    return (
        <div>
            <p>User settings</p>
            <button onClick={logoutCommand}>Log out</button>
            <div className="profile-grid">
                <div className="profile-grid-item">    
                    <img 
                        src={avatarSource} 
                        onError={()=> {setAvatarSource(`/rewer/uploads/avatars/default.jpg`)} }
                        className="image" 
                        alt="Avatar" 
                        title="Avatar"  />
                    <input ref={imageFile}  className="file-input" type="file" />
                    <button onClick={uploadImageFileCommand} type="button">Upload</button>
                    <p>{uploadMsg}</p>
                </div>
                <div className="profile-grid-item">
                    <h1>{localStorage.getItem("userName")}</h1>  
                    <textarea name="about" value={myProfile.about} onChange={bindMyProfile} rows={10} ></textarea>
                </div>
            </div>
            <div>
                <label>My nickname: </label>
                <input type="text" name="nickName" value={myProfile.nickName} onKeyDown={handleKeyDown} accept=".png,.jpg" onChange={bindMyProfile} />
                <button onClick={updateUserInfoCommand}  type="button" className="save" >Save</button>
            </div>
            <p>{nickNameMsg}</p>
            <div className="bottom-border"></div>
            <ChangePassword />
        </div>
    )
}
