import * as caller from "./caller"
import { IReviewLike } from "./reviewLike";

export interface IReview{
    id: number;
    movieId: number;
    userName: string;
    text: string;
    score: number;
    date: string;
    popIndex: number;
    nickName?: string;
    myLike?: IReviewLike;
}

export interface IUserReview extends IReview{
    movieTitle: string;
}

export async function insertReview(review: IReview){
    const response = await caller.POSTAsynch("/review/insertreview.php", review);
    return caller.getNewId(review, response);
}

export async function selectMyReview(movieId: number): Promise<IReview[]>{
    const response = await caller.POSTAsynch("/review/selectbyusernameandmovieid.php", {userName: localStorage.getItem("userName"), movieId: movieId, limit: 10, offset: 0});
    return caller.tryParse(response);
}

export async function updateReview(review: IReview): Promise<boolean>{
    const response = await caller.POSTAsynch("/review/updatereview.php", review);
    return caller.isResponse200(response);
}

export async function deleteReview(review: IReview): Promise<boolean>{
    const response = await caller.POSTAsynch("/review/deletereview.php", review);
    return caller.isResponse200(response);
}

export enum reviewOrderBy{
    popIndex = "popIndex",
    date = "date"
}
export async function selectReviewsByMovie(movieId: number, limit: number, offset: number, orderby: reviewOrderBy): Promise<IReview[]>{
    const response = await caller.POSTAsynch("/review/selectreviewsbymovie.php", {id: movieId, limit: limit, offset: offset, orderby});
    return caller.tryParse(response);
}

export async function selectReviewsByUser(userName: string, limit: number, offset: number, orderby: reviewOrderBy): Promise<IUserReview[]>{
    const response = await caller.POSTAsynch("/review/selectreviewsbyuser.php", {userName: userName, limit: limit, offset: offset, orderby});
    return caller.tryParse(response);
}

export async function selectFollowedReviews(limit: number, offset: number){
    const response = await caller.POSTAsynch("/review/selectfollowedreviews.php", { limit: limit, offset: offset });
    return caller.tryParse(response);
}