import React, { useState, useEffect } from 'react'
import { IArtistCast } from '../../objects/artist'
import { Link } from "react-router-dom"
import { getYear } from '../../dateParser'
interface IArtistCastRow{
    cast: IArtistCast;
}
export const ArtistCastRow: React.FC<IArtistCastRow> = ({cast}: IArtistCastRow)=> {
    const [castName, setCastName] = useState(cast.name);
    useEffect(()=>{
        if(cast.castTypeId > 3){
            setCastName(cast.name);
        } else {
           setNonActor();
        }
        function setNonActor(){
        if(cast.name === ""){
            setCastName(cast.castTypeName);
        } else {
            setCastName(`${cast.castTypeName} (${cast.name})`);
        }
    }
    }, [cast]);
    
    return (
        <div>
            <div className="cast-row">
            <img src={`/rewer/uploads/posters/${cast.movieId}.jpg`} alt={cast.movieName} />
            <span><Link to={`/movie?movieId=${cast.movieId}`} >{`${cast.movieName} (${getYear(cast.movieDate)})`}</Link></span>
            <p>{castName}</p>
        </div>
        </div>
    )
}

export default ArtistCastRow;
