import React, { useState, useRef } from 'react'
import * as User from "../../objects/user";
import { AuthUser, insertUser } from "../../objects/authUser"
import "./login.css";
interface SignUpForm{

}

export const SignUpForm: React.FC<SignUpForm> = ()=> {
    const [userTakenMsg, setUserTakenMsg] = useState("");
    const [msg, setMsg] = useState("");
    const userName = useRef<HTMLInputElement>();
    const nickName = useRef<HTMLInputElement>();
    const pw1 = useRef<HTMLInputElement>();
    const pw2 = useRef<HTMLInputElement>();

    async function checkCommand(){
        if(await User.doesExist(userName.current.value)){
            setUserTakenMsg("Taken");
        } else {
            setUserTakenMsg("Free");
        }
        
    }
    async function signUpCommand(){
        let canSend: boolean = true;
        if(await User.doesExist(userName.current.value)){
            setMsg("Username already taken.");
            setUserTakenMsg("Taken");
            canSend = false;
        }
        if(pw1.current.value !== pw2.current.value){
            console.log(pw1.current.value)
            console.log(pw2.current.value)
            setMsg(prevMsg => {
                return `${prevMsg} The two passwords don't match.`;
            });
            canSend = false;
        }
        if(canSend){
            sendSignUpRequest();
        }
    }

    async function sendSignUpRequest(){
        let newUser: AuthUser = {
            userName: userName.current.value,
            nickName: nickName.current.value,
            password: pw1.current.value,
            permission: 1
        }
        if(await insertUser(newUser)){
            setMsg("You have successfully signed up to our website!");
        } else {
            setMsg("Process failed.");
        }
    }

    return (
        <div>
            <h2>Sign up</h2>
                <div className="form-container">
                    <label className="form-item">New username:</label>
                    <input ref={userName} className="form-item" name="newUserName" placeholder="to log in" />
                    <div className="form-item"></div><div className="form-item"><span className="form-item">{userTakenMsg}</span><button onClick={checkCommand}>Check</button></div>
                    <label className="form-item">New nickname:</label>
                    <input ref={nickName} className="form-item" name="newNickName" placeholder="seen by others" />
                    <label className="form-item">New password:</label>
                    <input ref={pw1} className="form-item" type="password" name="newPassword1" placeholder="new password" />
                    <label className="form-item">New password again:</label>
                    <input ref={pw2} className="form-item" type="password" name="newPassword2" placeholder="new password again" />
                </div>
                <p>{msg}</p>
                <button onClick={signUpCommand} className="login-btn" type="button">Sign Up</button>        
        </div>
    )
}

export default SignUpForm