import * as caller from "./caller";
import { compareCastType, ICast } from "./cast"
export interface IMovie{
    id: number;
    title: string;
    releaseDate: Date;
    summary: string;
    runtime?: number;
    avgScore?: number;
    genres: IMovieGenre[];
    casts?: ICast[];
}

export interface IGenre{
    id: number;
    name: string;
}

export interface IMovieGenre{
    id: number;
    movieId: number;
    genreId: number;
    genreName: string;
}

export interface IDetailedMovie extends IMovie{
    casts: ICast[];
}

export async function insertMovie(movie: IMovie): Promise<IMovie>{
    const response = await caller.POSTAsynch("/movie/insertmovie.php", movie);
    return {...movie, id: caller.parseId(response)};
}

export async function selectMovie(id: number): Promise<IMovie>{
    const response = await caller.POSTAsynch("/movie/selectmovie.php", { id: id});
    return caller.tryParse(response)
}

export async function selectDetailedMovie(id: number): Promise<IDetailedMovie>{
    let data = {
        id: id
    }
    const response = await caller.POSTAsynch("/movie/selectdetailedmovie.php", data);
    let result: IDetailedMovie = caller.tryParse(response);
    result.casts.sort(compareCastType);
    result.genres.sort(compareMovieGenresByName); 
    return result;
}

export async function selectLatestMovies(limit: number): Promise<IMovie[]>{
    const response = await caller.POSTAsynch("/movie/selectlatestmovies.php", { limit: limit });
    return caller.tryParse(response);
}

export async function selectUpcomingMovies(limit: number): Promise<IMovie[]>{
    const response = await caller.POSTAsynch("/movie/selectupcomingmovies.php", { limit: limit });
    return caller.tryParse(response);
}

export async function updateMovie(movie: IMovie): Promise<boolean>{
    const response = await caller.POSTAsynch("/movie/updatemovie.php", movie);
    return caller.isResponse200(response);
}

export async function uploadPoster(formData: FormData){
    const response = await caller.UploadFile("/movie/uploadposter.php", formData);
    return caller.isResponse200(response);
}

export async function getGenres(){
    return caller.tryParse(await caller.GETAsynch("/moviegenre/getgenres.php")).sort(compareGenresByName);
}

export async function insertGenre(genre: IMovieGenre): Promise<IMovieGenre>{
    let newId = caller.parseId(await caller.POSTAsynch("/moviegenre/insertgenre.php", genre));
    return {...genre, id: newId};
}

export async function deleteGenre(id: number): Promise<boolean>{
    return caller.isResponse200(await caller.POSTAsynch("/moviegenre/deletegenre.php", { id: id}))
}

export function compareGenresByName(genre1: IGenre, genre2: IGenre){
    return genre1.name.localeCompare(genre2.name);
}

export function compareMovieGenresByName(genre1: IMovieGenre, genre2: IMovieGenre){
    return genre1.genreName.localeCompare(genre2.genreName);
}