import React, { useState, useEffect, useRef } from 'react'
import CastList from '../components/cast/CastList';
import MovieInfo from '../components/movie/MovieInfo'
import { MyReview } from '../components/review/MyReview';
import ReviewList from '../components/review/ReviewList';
import ReviewOrderBySelect from '../components/review/ReviewOrderBySelect';
import { normalFormat } from '../dateParser';
import { useAsyncReference } from '../hooks';
import { selectDetailedMovie } from '../objects/movie';
import { IReview, reviewOrderBy, selectReviewsByMovie } from '../objects/review';
import { getParameter } from '../urlManager';
interface IMoviePage{

}
export default function Movie() {
    const [movie, setMovie] = useState({id: parseInt(getParameter("movieId")), title: "", releaseDate: new Date(), summary: "", genres: [], casts: []});
    const [actors, setActors] = useState([]);
    const [reviews, setReviews] = useAsyncReference([]);
    const [reviewHeader, setReviewHeader] = useState("");
    const reviewPerPage = 5;
    const reviewDiv = useRef<HTMLDivElement>();
    const [orderBy, setOrderBy] = useState(undefined);
    useEffect(()=>{
        requestMovie();
        return ()=> {
            removeWindowListener();
        }
    }, []);

    useEffect(()=>{
        if(orderBy){
            requestReviews();
        }
        return ()=> {
            removeWindowListener();
        }
    }, [orderBy]);


    async function requestMovie(){
        let selectedMovie = await selectDetailedMovie(movie.id);
        if(selectedMovie){
            setMovie(selectedMovie);
            setActors(selectedMovie.casts.filter(c=> c.castTypeId > 3));
            if(new Date().getTime() > new Date(selectedMovie.releaseDate).getTime()){
                setOrderBy(reviewOrderBy.popIndex);
            } else {
                reviewDiv.current.style.display = "none";
                setReviewHeader(`Reviews will be available after ${normalFormat(selectedMovie.releaseDate.toString())}`)
            }
        }
    }

    async function requestReviews(){
        let requestedReviews = await selectReviewsByMovie(movie.id, reviewPerPage, 0, orderBy);
        if(requestedReviews){;
            setReviews(requestedReviews);
            setReviewHeader("Reviews:");
            if(requestedReviews.length === reviewPerPage) addWindowListener();                
        } else {
            alert("Server errror: could not download reviewdata.");
        }
    }

    function addWindowListener(){
        window.addEventListener("scroll", windowListener);
    }

    function removeWindowListener(){
        window.removeEventListener("scroll", windowListener);
    }

    function windowListener(){
        if ((window.innerHeight + window.scrollY) >= (document.body.offsetHeight*0.9)) {
            getMoreReviews();
        }
    }

    async function getMoreReviews(){
        removeWindowListener();
        let requestedReviews = await selectReviewsByMovie(movie.id, reviewPerPage, reviews.current.length, orderBy);
        if(requestedReviews){
            setReviews([...reviews.current, ...requestedReviews]);
            if(requestedReviews.length === reviewPerPage) addWindowListener();
        } else {
            alert("Server errror: could not download reviewdata.");
        }
    }

    return (
        <div className="main-container" >
            <MovieInfo movie={movie} />
            <h2>Cast</h2>
            <CastList casts={actors} />
            <MyReview movie={movie} />
            <h2>{reviewHeader}</h2>
            <div ref={reviewDiv}>
                <ReviewOrderBySelect defaultValue={orderBy} onUpdate={(newValue: reviewOrderBy)=> { setOrderBy(newValue); }} />
                <ReviewList reviews={reviews.current} />
            </div>
        </div>
    )
}
