import React, { useRef } from 'react'
import { IGenre } from '../../objects/movie'

interface IGenreSelect{
    genres: IGenre[];
    updateSelected: (selectedValue: string)=> void;
}

export const GenreSelect: React.FC<IGenreSelect> = ({genres, updateSelected}: IGenreSelect)=> {
    const select = useRef<HTMLSelectElement>();
    return (
        <select ref={select} onChange={()=> { updateSelected(select.current.value) }} >
            {genres.map(g=> {
                return <option key={`opt${g.id}`} value={g.id}>{g.name}</option>
            })}
        </select>
    )
}
