import * as caller from "./caller";
import { ICast } from "./cast";
export interface IArtist{
    id: number;
    name: string;
    birthPlace: string;
    birthDate: Date;
    deathPlace: string;
    deathDate: Date;
    bio: string;
}

export interface IDetailedArtist extends IArtist{
    casts: IArtistCast[];
}

export interface IArtistCast extends ICast{
    movieDate: string;
    castTypeName: string;
}

export async function insertArtist(artist: IArtist): Promise<boolean>{
    const response = await caller.POSTAsynch("/artist/insertartist.php", artist);
    return caller.getNewId(artist, response);
}

export async function selectArtist(id: number): Promise<IArtist>{
    const data = {
        id: id
    }
    const response = await caller.POSTAsynch("/artist/selectartist.php", data);
    return caller.tryParse(response);
}

export async function updateArtist(artist: IArtist): Promise<boolean>{
    const response = await caller.POSTAsynch("/artist/updateartist.php", artist);
    return caller.isResponse200(response);
}

export async function uploadPortrait(file: FormData): Promise<boolean>{
    const response = await caller.UploadFile("/artist/uploadportrait.php", file);
    return caller.isResponse200(response);
}

export async function selectArtistsByKeyword(keyword: string, limit: number, offset: number): Promise<IArtist[]>{
    const response = await caller.POSTAsynch("/artist/selectartistsbykeyword.php", {keyword: keyword, limit: limit, offset: offset});
    return caller.tryParse(response);
}

export async function selectDetailedArtist(id: number): Promise<IDetailedArtist>{
    const detailedArtist: IDetailedArtist = caller.tryParse(await caller.POSTAsynch("/artist/selectdetailedartist.php", {id: id}));
    detailedArtist.casts.sort(compareDate);
    return detailedArtist;
}

export function compareDate(cast1: IArtistCast, cast2: IArtistCast){
    if(new Date(cast1.movieDate) < new Date(cast2.movieDate)){
        return -1;
    }
    if(new Date(cast1.movieDate) > new Date(cast2.movieDate)){
        return 1;
    }
    return 0;
}