import React, { useRef, useEffect, useState } from 'react'
import "./headerStyle.css";
// @ts-ignore
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars, faUser, faSignInAlt } from '@fortawesome/free-solid-svg-icons'
import { HeaderItem, IHeaderItem } from './HeaderItem';
import { createUrl } from '../../urlManager';

interface INavList{

}

export const Header: React.FC<INavList> = ()=> {
   const headerRef = useRef<HTMLDivElement>(null);
   const [userIcon, setUserIcon] = useState(null);
   const [optionalHeaders, setOptionalHeaders] = useState<IHeaderItem[]>([]);
    let sticky: number;
    window.onscroll = function(){
        headerHandler();
    }

    function headerHandler(){
        sticky = headerRef.current.offsetTop;
        if(window.pageYOffset > sticky){
            headerRef.current.classList.add("sticky");
        } else {
            headerRef.current.classList.add("sticky");
        }
    } 
    function displayMenu(){
        if(!headerRef.current.classList.contains("responsive")){
            headerRef.current.classList.add("responsive");
            document.body.scrollTop = 0; // For Safari
            document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
        } else {
            headerRef.current.classList.remove("responsive");
        }
    }

    useEffect(()=>{
        console.log(localStorage.getItem("userName"));
        console.log(localStorage.getItem("nickName"));
        console.log(localStorage.getItem("session"));
        console.log(localStorage.getItem("permission"));
        if(parseInt(localStorage.getItem("permission")) > 1){
            setOptionalHeaders(prevOptionals => {
                return [...prevOptionals, {to: "/manage", content: "Manage"}];
            },)
        }
        if(localStorage.getItem("userName") != null){
            setUserIcon(<FontAwesomeIcon icon={faUser} />);
        } else {
            setUserIcon(<FontAwesomeIcon icon={faSignInAlt} />);
           
        }
    }, []);
    //<FontAwesomeIcon icon={faUser} />
    return (        
           <header>
                <div ref={headerRef} className="topnav">
                    <HeaderItem to="/profile" content={userIcon} />
                    <HeaderItem to="/rewer" content="Home" />
                    <HeaderItem to={createUrl("/movies", [{name: "name", value: "mvie"}, {name: "search", value: "sll"}])} content="Movies" />
                    <HeaderItem to={"/stars"} content="Stars" />
                    <HeaderItem to={"/follows"} content="Follows" />
                    {optionalHeaders.map(oh => 
                        <HeaderItem key={`${oh.to}${oh.content}`} to={oh.to} content={oh.content} />
                    )}
                    <a onClick={displayMenu} className="icon"><FontAwesomeIcon icon={faBars} /></a>
                </div>
            </header>
    )
}
export default Header;
