import React from 'react'
import { ICast } from '../../objects/cast';
import { Link } from "react-router-dom"

interface ICastRow{
    cast: ICast;
}

export const CastRow: React.FC<ICastRow> = ({cast}: ICastRow) => {
    return (
        <div className="cast-row">
            <img src={`/rewer/uploads/portraits/${cast.artistId}.jpg`} alt={cast.artistName} />
            <span><Link to={`/star?artistId=${cast.artistId}`} >{cast.artistName}</Link></span>
            <p className="cast-row-item">{cast.name}</p>
        </div>
    )
}

export default CastRow;