import React, { useState, useEffect } from 'react'
import { useAsyncBinder, onEnter } from '../../hooks'
import { IArtist, selectArtist, selectArtistsByKeyword } from '../../objects/artist';
import { compareCastType, ICast, insertCast, selectCastsByMovie } from '../../objects/cast';
import { getCastTypes } from '../../objects/castType';
import { getParameter } from '../../urlManager';
import ArtistSelector from '../artist/ArtistSelector';
import CastTypeSelect from '../cast/CastTypeSelect'
import { EditCastList } from '../cast/EditCastList';
import { Link } from  "react-router-dom";
import { IMovie } from '../../objects/movie';
interface IEditCastList{
    movie: React.MutableRefObject<IMovie>;
    setMovie: (movie: IMovie) => void;
}
export const EditCast: React.FC<IEditCastList> = ({movie, setMovie}: IEditCastList) => {
    const [castTypes, setCastTypes] = useState([]);
    const [artists, setArtists] = useState([]);
    //const [castList, setCastList] = useState([]);
    const [keyword, setKeyword, bindKeyword] = useAsyncBinder({search: ""});
    const [newCast, setNewCast, bindNewCast] = useAsyncBinder({id: 0, movieId: movie.current.id, artistId: 0, castTypeId: 1, name: "", artistName: "Not selected"});
    useEffect(()=>{
        fetchRequests();
    }, []);
    async function fetchRequests(){
        await Promise.all([requestCastTypes(), requestArtists()]);
    }
    async function requestCastTypes(){
        setCastTypes(await getCastTypes());
    }
    async function requestArtists(){
        setArtists(await selectArtistsByKeyword(keyword.current.search, 25, 0));
    }

    async function addCast(){
        if(movie.current.id === 0 || movie.current.id == null){
            alert("You must create the movie's page first.");
            return false;
        }
        setNewCast({...newCast.current, movieId: movie.current.id});
        if(newCast.current.artistId === 0 || newCast.current.artistId == null){
            alert("Choose an artist!");
            return false;
        } else if(await insertCast(newCast.current)){
            setCasts([...movie.current.casts, newCast.current].sort(compareCastType));
            setNewCast({...newCast.current, id: 0, name: ""});
            return true;
        } else {
            alert("Server error.");
            return false;
        } 
    }

    function setCasts(newCasts: ICast[]){
        setMovie({...movie.current, casts: newCasts});
    }

    function updateNewCastArtist(artist: IArtist){
        setNewCast({...newCast.current, artistId: artist.id, artistName: artist.name});
    }

    function updateNewCastType(newValue: string){
        setNewCast({...newCast.current, castTypeId: parseInt(newValue)});
    }
    
    return (
        <div>
            <div>
                <h2>Cast: </h2>
                <EditCastList castList={movie.current.casts} setCastList={setCasts} castTypes={castTypes} />
            </div>
            <div className="adder-panel">
                <h3>Add cast member:</h3>
                <span className="artist-name" >{newCast.current.artistName}</span>
                <CastTypeSelect typeList={castTypes} updateValue={updateNewCastType} />
                <input value={newCast.current.name} onChange={bindNewCast} name="name" placeholder="As" />
                <button onClick={addCast}>Add</button>
                <Link to="/editartist?artistId=0">Create new star</Link>
            </div>
            <div>
                <input value={keyword.current.search} name="search" onChange={bindKeyword} onKeyDown={(e)=> { onEnter(e, requestArtists); }} placeholder="Search" />
                <button onClick={requestArtists}>Search</button>
                <ArtistSelector artistList={artists} onSelectionChanged={updateNewCastArtist} />
            </div>
        </div>
    )
}
