import * as caller from "./caller"

export interface IReviewLike{
    id: number;
    reviewId: number;
    userName: string;
    value: number;
}

export async function insertReviewLike(reviewLike: IReviewLike): Promise<IReviewLike>{
    const response = await caller.POSTAsynch("/review/insertreviewlike.php", reviewLike);
    return {...reviewLike, id: caller.parseId(response)};
}

export async function updateReviewLike(reviewLike: IReviewLike):Promise<boolean>{
    const response = await caller.POSTAsynch("/review/updatereviewlike.php", reviewLike);
    return caller.isResponse200(response);
}