import React, { Fragment, useState, useEffect } from 'react'
import { normalFormat } from '../../dateParser'
import { IArtist, IDetailedArtist } from '../../objects/artist'

interface IStarInfo{
    artist: IDetailedArtist;
}

export const StarInfo: React.FC<IStarInfo> = ({artist}: IStarInfo) => {
    const [died, setDied] = useState(null);
    const deathInfo = 
    <Fragment>
        <p>Died: <i>{`${normalFormat(artist.deathDate.toString())},`}</i></p>
        <p><i>{artist.deathPlace}</i></p>
    </Fragment>;

    const [bio, setBio] = useState(<p></p>);
    const briefBio = <Fragment><p>{`${artist.bio.substring(0, 1500)}...`}</p><p onClick={showAll} className="show-more">Show more</p></Fragment>;
    useEffect(()=>{
        if(artist.deathPlace != null && artist.deathPlace !== ""){
            setDied(deathInfo);
        } else {
            setDied(<Fragment></Fragment>);
        }
    }, [artist]);

    useEffect(()=>{
        if(artist.bio.length > 1500){
            showBrief();
        } else {
            showAll();
        }
    }, [artist])

    function showAll(){
        setBio(<Fragment><p>{artist.bio}</p><p onClick={showBrief} className="show-more">Show less</p></Fragment>);
    }

    function showBrief(){
        setBio(briefBio);
    }

    return (
        <div>
            <h1>{artist.name}</h1>
            <div className="info-grid">
                <div className="info-grid-item right-border">
                    <img className="portrait" src={`/rewer/uploads/portraits/${artist.id}.jpg`} alt={`${artist.name}_portrait`} />
                    <p className="border-bottom">Born: <i>{`${normalFormat(artist.birthDate.toString())},`}</i></p>
                    <p><i>{artist.birthPlace}</i></p>
                    {died}
                </div>
                <div className="info-grid-item">
                    {bio}
                </div>
            </div>
        </div>
    )
}
