import React, { useState } from 'react'
import { IMovie } from '../../objects/movie'
import { IReview } from '../../objects/review';
import { ReviewRow } from './ReviewRow';

interface IReviewList{
    reviews: IReview[];
}

export const ReviewList: React.FC<IReviewList> = ({reviews}: IReviewList) => {
    return (
        <div>
            {reviews.map(r=>{
                return <ReviewRow key={`review-${r.id}`} review={r} />
            })}
        </div>
    )
}

export default ReviewList;