import React from 'react'
import { ICast } from '../../objects/cast';
import CastRow from './CastRow';

interface ICastList{
    casts: ICast[];
}

export const CastList: React.FC<ICastList> = ({casts}: ICastList) => {
    return (
        <div>
            {casts.map(c=> {
                return <CastRow key={`cast-${c.id}`} cast={c} />
            })}
        </div>
    )
}

export default CastList;