import React, { Fragment, useEffect, useState, useRef } from 'react'
import { useAsyncBinder } from '../../hooks';
import { IArtist, insertArtist, selectArtist, updateArtist, uploadPortrait } from '../../objects/artist';
import { changeParam, getParameter } from '../../urlManager';
import "./artistStyle.css";

interface IEditDetails{
    artistId: number;
}

export const EditDetails: React.FC<IEditDetails> = (props: IEditDetails) => {
    const imageInput = useRef<HTMLInputElement>();
    const createButton = <button onClick={createArtist}>Create</button>;
    const saveButton = <button onClick={saveArtist}>Save</button>;
    const newAvatarBottom = <Fragment>
                                <p>Create the artist's page to modify their portrait.</p>
                            </Fragment>;
    const editAvatarBottom = <Fragment>
                                <input ref={imageInput} type="file" accept=".png,.jpg" className="file-input" />
                                <p>It is recommended to use pictures with the standard portrait image ratio (9:16)</p>
                                <button onClick={uploadImage} >Upload</button>
                            </Fragment>;
    const [artist, setArtist, bindArtist] = useAsyncBinder({id: parseInt(getParameter("artistId")), name: "", birthPlace: "", birthDate: new Date(), deathPlace: "", deathDate: new Date(), bio: ""});
    const [detailButton, setDetailButton] = useState(<Fragment></Fragment>);
    const [mainMsg, setMainMsg] = useState("");
    const [portraitBottom, setPortraitBottom] = useState(newAvatarBottom);
    const [portraitSource, setPortraitSource] = useState("");

    useEffect(()=>{
        if(artist.current.id > 0){
            renderEditArtistMode();
        } else {
            renderNewArtistMode();
        }
        setPortraitSource(`/rewer/uploads/portraits/${artist.current.id}.jpg`);
    }, []);// eslint-disable-line react-hooks/exhaustive-deps

    async function renderEditArtistMode(){
        let selectedArtist = await selectArtist(parseInt(getParameter("artistId")));
        if(selectedArtist != null){
            setArtist(selectedArtist);
            setDetailButton(saveButton);
            setPortraitBottom(editAvatarBottom);
        } else {
            setMainMsg("Error trying to load artistpage.") //TODO go to 404 page
        }
    }
    
    function renderNewArtistMode(){
        setDetailButton(createButton);
        setPortraitBottom(newAvatarBottom);
    }

    async function createArtist(){
        await insertArtist(artist.current);
        if(artist.current.id != null){
            setMainMsg("Artist's page successfully created.");
            changeParam({name: "artistId", value: artist.current.id.toString()});
            renderEditArtistMode();
        } else{
            setMainMsg("Internal error: the server was unable to fulfill your request.");
        } 
    }

    async function saveArtist(){
        if(updateArtist(artist.current)){
            setMainMsg("Changes saved.");
        } else {
            setMainMsg("WARNING: your last attempt to save your work was declined by the server.");
            alert("Request failed: server error.");
        }        
    }

    async function uploadImage(){
        const formData = new FormData();
        formData.append("imageFile", imageInput.current.files[0], `${artist.current.id}.jpg`);
        if(await uploadPortrait(formData)){
            setPortraitSource(`/rewer/uploads/portraits/${artist.current.id}.jpg?${new Date().getTime()}`);
        } else {
            alert("Process failed. Make sure file's size is not bigger than 10MB.");
        }
    }
    return (
        <div>
            {mainMsg}
            <br/>
            {detailButton}
            <br/>
            <input value={artist.current.name} onChange={bindArtist} type="text" name="name" placeholder="Name" className="name-input" />
            <div className="detail-grid">
                <div className="detail-grid-item span-2">
                    <h3>Born: </h3>
                    <input value={artist.current.birthPlace} type="text" onChange={bindArtist} name="birthPlace" className="place-input" placeholder="Birthplace"/>
                    <input value={artist.current.birthDate.toString()} type="date" onChange={bindArtist} name="birthDate" /><br/>
                    <h3>Died: </h3>
                    <input value={artist.current.deathPlace} type="text" onChange={bindArtist} name="deathPlace" className="place-input" placeholder="leave empty if irrelevant" title="leave empty if irrelevant"/>
                    <input value={artist.current.deathDate.toString()} type="date" onChange={bindArtist} name="deathDate" />
                    <textarea value={artist.current.bio} onChange={bindArtist} name="bio" placeholder="Biography" rows={30} />
                </div>
                <div className="detail-grid-item first" >
                    <img className="portrait" src={portraitSource} onError={()=> {setPortraitSource("/rewer/uploads/portraits/0.jpg")}} alt="Portrait" /><br />
                    {portraitBottom}
                </div>
            </div>
            
            
        </div>
    )
}

export default EditDetails;