import React, { useState, useEffect } from 'react'
import { StarInfo } from '../components/artist/StarInfo';
import ArtistCastList from '../components/cast/ArtistCastList';
import { selectDetailedArtist } from '../objects/artist';
import { getParameter } from '../urlManager';

export default function Star() {
    const [artist, setArtist] = useState({id: parseInt(getParameter("artistId")), name: "", birthPlace: "", birthDate: new Date(), deathPlace: "", deathDate: new Date(), bio: "", casts: []});
    useEffect(()=>{
        requestArtist();
    }, []);
    async function requestArtist(){
        const detailedArtist = await selectDetailedArtist(artist.id);
        if(detailedArtist){
            setArtist(detailedArtist);
        } else {
            alert("Server error.");
        }
    }
    return (
        <div className="main-container">
            <StarInfo artist={artist} />
            <ArtistCastList casts={artist.casts} />
        </div>
    )
}
