export function getMyInfo(){
    let cookie = {};
    document.cookie.split(';').forEach(function(el) {
      let [k,v] = el.split('=');
      cookie[k.trim()] = v;
    });
    let me = {
        userName: decodeURIComponent(cookie["userName"]).replace("+", " "),
        nickName: decodeURIComponent(cookie["nickName"]).replace("+", " "),
        session: decodeURIComponent(cookie["session"])
    }
    return me;
}

export const itemReplacer = (array, oldItem, newItem) => {
  array.map(item=> item === oldItem? newItem : item);
}

export const replacedList = (array, oldItem, newItem) => {
  return array.map(item=> item === oldItem? newItem : item);
}
