import React, { useState, useEffect, Fragment, useRef } from 'react'
import { Link } from "react-router-dom"
import { IMovie } from '../../objects/movie'
import "./movieStyle.css";
import MovieDiv from './MovieDiv';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faForward, faBackward } from '@fortawesome/free-solid-svg-icons'

interface IMovieSlider{
    movies: IMovie[];
}

export const MovieSlider: React.FC<IMovieSlider> = ({movies}: IMovieSlider) => {
    const nextBtn = useRef<HTMLButtonElement>();
    const prevBtn = useRef<HTMLButtonElement>();
    const [getMovies, setMovies] = useState(movies.slice(0, 3));
    const [pageIndex, setPageIndex] = useState(0);
    useEffect(()=>{
        setMovies(movies.slice(0, 3));
        if(movies.length <= 3){
            nextBtn.current.style.visibility = "hidden";
            prevBtn.current.style.visibility = "hidden";
        } else {
            nextBtn.current.style.visibility = "visible";
            prevBtn.current.style.visibility = "visible";
        }
    }, [movies]);

    useEffect(()=>{
        setMovies(movies.slice(pageIndex * 3, (pageIndex * 3)+3));
    }, [pageIndex])

    function next(){
        if((pageIndex+1) * 3 >= movies.length){
            setPageIndex(0);
        } else {
            setPageIndex(old => { return old + 1} );
        }
    }

    function prev(){
        if((pageIndex-1) < 0 ){
            setPageIndex(Math.ceil(movies.length / 3)-1);
        } else {
            setPageIndex(old => { return old - 1} );
        }
    }
    return (
        <Fragment>
        <div className="slide-grid">
            <button ref={nextBtn} className="slide-btn" onClick={prev}><FontAwesomeIcon icon={faBackward} /></button>
            <div className="movie-grid">
                {getMovies.map(m=>{
                    return <Link to={`/movie?movieId=${m.id}`} className="movie-anchor" key={`latest-${m.id}`} ><MovieDiv  movie={m} /></Link>
                })}
            </div>
            <button ref={prevBtn} className="slide-btn" onClick={next}><FontAwesomeIcon icon={faForward} /></button> 
        </div>
        </Fragment>
    )
}

export default MovieSlider;