import * as caller from "./caller";
export interface ICast{
    id: number;
    movieId: number;
    artistId: number;
    castTypeId: number;
    name: string;
    movieName?: string;
    artistName?: string;
}

export async function selectCastsByMovie(movieId: number): Promise<ICast[]>{
    const response = await caller.POSTAsynch("/cast/selectcastsbymovie.php", {id: movieId});
    return caller.tryParse(response);
}

export async function insertCast(cast: ICast): Promise<boolean>{
    const response = await caller.POSTAsynch("/cast/insertcast.php", cast);
    return caller.getNewId(cast, response);
}

export async function updateCast(cast: ICast): Promise<boolean>{
    const response = await caller.POSTAsynch("/cast/updatecast.php", cast);
    return caller.isResponse200(response);
}

export async function deleteCast(castId: number): Promise<boolean>{
    const response = await caller.POSTAsynch("/cast/deletecast.php", {id: castId});
    return caller.isResponse200(response);
}

export function compareCastType(cast1: ICast, cast2: ICast){
    if(cast1.castTypeId < cast2.castTypeId){
        return -1;
    }
    if(cast1.castTypeId > cast2.castTypeId){
        return 1;
    }
    return 0;
}