//const host: string = `${window.location.origin}/rewer/php/requests`;
const def: string = "/rewer/php/requests";
export async function GETAsynch(url: string){
    const response = await fetch(def+url, {
        method: "GET",
        cache: "no-cache",
        credentials: "include"
    });
    if(response.ok){
        return response.text();
    } else {
        console.log(response);
        return response.status.toString();
    }
}

export async function UploadFile(url, data: FormData): Promise<string>{
    const response = await fetch(def + url, {
        method: "POST",
        cache: 'no-cache',
        body: data
    });
    if(response.ok){
        return response.text();
    } else {
        console.log(response);
        return response.status.toString();
    }
}

export async function POSTAsynch(url: string, data: any): Promise<string> {
    // Default options are marked with *
    const response = await fetch(def+url, {
      method: 'POST', // *GET, POST, PUT, DELETE, etc.
      mode: 'cors', // no-cors, *cors, same-origin
      cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
      credentials: 'same-origin', // include, *same-origin, omit
      headers: {
        'Content-Type': 'application/json'
        // 'Content-Type': 'application/x-www-form-urlencoded',
      },
      redirect: 'follow', // manual, *follow, error
      referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
      body: JSON.stringify(data) // body data type must match "Content-Type" header
    });
    if(response.ok){
        return response.text();
    } else {
        console.log(response);
        return response.status.toString();
    }
}

export function IsSuccessful(json: string){
    if(JSON.parse(json).msg === "success"){
        return true;
    }
    console.log(json);
    return false;
}

export function isResponse200(response: string){
    try{
        if(JSON.parse(response).response === 200) return true;
    } catch(err){
        console.log(err);
        console.log(response);
    }
}

export function tryParse(response: string){
    try{
        return JSON.parse(response).object;
    }catch(err) {
        console.log(err);
        console.log(response);
    }
    return null;
}

export function getNewId(object: any, response: string){
    try{
        object.id = JSON.parse(response).newId;
        if(object.id != null) return true;
        console.log(response);
    } catch(err){
        console.log(err);
        console.log(response);
    }
    return false;
}

export function parseId(response: string): number{
    try{
        return JSON.parse(response).newId;
    } catch(err){
        console.log(err);
        console.log(response);
    }
    return null;
}
