import React, { Fragment, useRef, useState } from 'react'
import { updatePassword } from '../../objects/authUser';
import "../login/login.css";
interface IChangePassword{

}

export const ChangePassword: React.FC<IChangePassword> = ()=> {
    const [oldPw, setOldPw] = useState("");
    const [newPw1, setNewPw1] = useState("");
    const [newPw2, setNewPw2] = useState("");
    const [msg, setMsg] = useState("");
    async function updatePasswordCommand(){
        if(newPw1 !== newPw2){
            setMsg("The two passwords don't match");
            clearInput();
            return false;
        }
        if(await updatePassword({ userName: localStorage.getItem("userName"), password: oldPw, permission: 0, nickName: localStorage.getItem("nickName")}, newPw1)){
            setMsg("You password has been updated.");
            clearInput();
            return true;
        } else {
            setMsg("Access denied.");
            clearInput();
            return false;
        }
    }

    function clearInput(){
        setOldPw("");
        setNewPw1("");
        setNewPw2("");
    }

    function handleKeyDown(e){
        if(e.key === 'Enter'){
            updatePasswordCommand();
        }
    }

    return (
       <Fragment>
            <h2>Update password</h2>
            <div className="form-container">
                <label className="form-item">Current password:</label>
                <input value={oldPw} onChange={e=> setOldPw(e.target.value)} type="password" className="form-item" placeholder="old password" />
                <label className="form-item">New password:</label>
                <input value={newPw1} onChange={e=> setNewPw1(e.target.value)} type="password" className="form-item" placeholder="new password" />
                <label className="form-item">New password again:</label>
                <input value={newPw2} onChange={e=> setNewPw2(e.target.value)} onKeyDown={handleKeyDown} type="password" className="form-item" placeholder="new password to check" />
                <p>{msg}</p>
            </div>
            <button onClick={updatePasswordCommand} type="button">Update password</button>
       </Fragment>
    )
}
export default ChangePassword;