import { User } from "./user";
import * as caller from "./caller";
import { faSlidersH } from "@fortawesome/free-solid-svg-icons";
export interface AuthUser extends User{
    password: string;
    permission: number;
    session?: string;
}

interface loginResponse{
    user: AuthUser;
    isSuccessful: boolean;
}

interface isLoggedInResponse{
    isLoggedIn: boolean;
    setNewSession?: boolean;
    newSession?: string;
}

export async function login(authUser: AuthUser, autoLogin: boolean):Promise<boolean>{
    const data = {
        authUser: authUser,
        autoLogin: autoLogin
    }
    const response = await caller.POSTAsynch("/user/login.php", data);
    let parsedResponse: loginResponse;
    try{
        parsedResponse = JSON.parse(response).response;
    } catch(err){
        console.log(err);
        console.log(response);
        return false;
    }
    console.log(parsedResponse);
    if(!parsedResponse.isSuccessful){
        localStorage.clear();
        return false;
    } 
    
    localStorage.clear();
    localStorage.setItem("userName", parsedResponse.user.userName);
    localStorage.setItem("nickName", parsedResponse.user.nickName);
    console.log(parsedResponse.user.permission.toString());
    localStorage.setItem("permission", parsedResponse.user.permission.toString());
    if(autoLogin){
        localStorage.setItem("session", parsedResponse.user.session);
    }
    return true;
}

export async function insertUser(authUser: AuthUser): Promise<boolean>{
    const response = await caller.POSTAsynch("/user/insertuser.php", authUser);
    return caller.isResponse200(response);
}

export async function isLoggedIn(): Promise<isLoggedInResponse>{
    if(!localStorage.hasOwnProperty("userName")){
        return {isLoggedIn: false};
    }
    const response = await caller.POSTAsynch("/user/isloggedin.php", { userName: localStorage.getItem("userName"), session: localStorage.getItem("session")});
    try{
        return JSON.parse(response).response;
    } catch(err){
        console.log(err);
        console.log(response);
    }
    return {isLoggedIn: false};
}

export async function updatePassword(user: AuthUser, newPassword: string): Promise<boolean>{
    let data = {
        user: user,
        newPassword: newPassword
    }
    const response = await caller.POSTAsynch("/user/updatepassword.php", data);
    return caller.isResponse200(response);
}

export async function logOut(){
    await caller.GETAsynch("/user/logout.php");
    localStorage.clear();
}

export async function handleLogin(): Promise<Boolean>{
    let response:any = await isLoggedIn();
    if(response.isLoggedIn === false){
        localStorage.clear();
        return false;
    }
    if(response.setNewSession){
        localStorage.setItem("session", response.newSession);
    }
    return true;
}