import React, { useState, useEffect } from 'react'
import { IReview, IUserReview } from '../../objects/review'
import { ReviewRow } from './ReviewRow'
import { Link } from "react-router-dom"

interface IFollowedReview{
    review: IUserReview;
}

export const FollowedReviewRow: React.FC<IFollowedReview> = ({review}: IFollowedReview)=> {
    const [imgsrc, setImgsrc] = useState(`/rewer/uploads/posters/${review.movieId}.jpg`);
    useEffect(()=>{
    }, []); 
    
    return (
        <div className="followed-review-grid">
            <div className="review-poster-container">
                <Link to={`/movie?movieId=${review.movieId}`} >
                    <img src={imgsrc} alt={`${review.movieTitle}-poster`} onError={()=> { setImgsrc(`/rewer/uploads/posters/0.jpg`)}} />
                    <p>{review.movieTitle}</p>
                </Link>
            </div>
            <div>
                <ReviewRow review={review} />
            </div>
        </div>
    )
}

export default FollowedReviewRow;