import React, { useRef } from 'react'
import { reviewOrderBy } from '../../objects/review';

interface IReviewOrderBySelect{
   defaultValue: reviewOrderBy;
   onUpdate: (newValue: reviewOrderBy) => void;
}

export const ReviewOrderBySelect: React.FC<IReviewOrderBySelect> = ({defaultValue, onUpdate}: IReviewOrderBySelect) => {
    const select = useRef<HTMLSelectElement>();
    function update(){
        onUpdate(reviewOrderBy[select.current.value]);
    }
    return (
        <select ref={select} onChange={update} value={defaultValue}>
            <option value={reviewOrderBy.popIndex}>Most popular</option>
            <option value={reviewOrderBy.date}>Latest</option>
        </select>
    )
}

export default ReviewOrderBySelect;
