import { readConfigFile} from "typescript";
import React, { Fragment, useState, useRef, useEffect } from "react";
import { changeParam, getParameter } from '../../urlManager';
import "./movieStyle.css";
import { IMovie, insertMovie, selectMovie, updateMovie, uploadPoster } from "../../objects/movie";
import { setBinder, useAsyncReference } from "../../hooks";

interface IEditMovieDetails{
    movie: React.MutableRefObject<IMovie>;
    setMovie: (movie: IMovie)=>void;
    movieBinder: (e)=>void;
}

export const EditMovieDetails: React.FC<IEditMovieDetails> = ({movie, movieBinder, setMovie}: IEditMovieDetails) =>{
    const posterInput = useRef<HTMLInputElement>();
    const createBtn = <button onClick={createMovie} type="button">Create Movie's Page</button>;
    const saveBtn =  <button onClick={saveMovie} type="button">Save Changes</button>
    const [mainBtn, setMainBtn] = useState(createBtn);
    const newPosterBottom = 
        <Fragment>
            <p>Create the movie's page to edit this picture.</p>
        </Fragment>
    const editPosterBottom = 
        <Fragment>
            <p>It is recommended to use an image with a standard poster image ratio</p>
            <input ref={posterInput} type="file" accept=".png,.jpg,.jpeg" className="file-input" />
            <button onClick={savePoster}>Upload</button>
        </Fragment>
    const [posterBottom, setPosterBottom] = useState(newPosterBottom);
    const [posterSource, setPosterSource] = useState("");
    const [mainMsg, setMainMsg] = useState("");
    useEffect(()=>{
        if(movie.current.id > 0){
            renderEditMode();
        } else {
            setPosterSource(`/rewer/uploads/posters/0.jpg?`)
        }
    },[]);

    async function savePoster(){
        const formData = new FormData();
        formData.append("imageFile", posterInput.current.files[0], `${movie.current.id}.jpg`);
        if(await uploadPoster(formData)){
            setPosterSource(`/rewer/uploads/posters/${movie.current.id}.jpg?${new Date().getTime()}`);
        } else {
            alert("Upload unsuccessful");
        }
    }

    async function renderEditMode(){
        setMainBtn(saveBtn);
        setPosterBottom(editPosterBottom);
        setPosterSource(`/rewer/uploads/posters/${movie.current.id}.jpg?${new Date().getTime()}`);
    }

    async function createMovie(){
        const newMovie = await insertMovie(movie.current);
        if(newMovie != null && newMovie.id != null){
            setMainMsg("Movie's page was created successfully.");
            changeParam({name: "movieId", value: newMovie.id.toString()});
            setMovie(newMovie);
            renderEditMode();
        } else {
            setMainMsg("Internal error: the server was unable to fulfill your request.");
        }
    }

    async function saveMovie(){
        if(await updateMovie(movie.current)){
            setMainMsg(`Changes have been successfully saved: ${new Date().getHours()}:${new Date().getMinutes()}`);
        } else {
            setMainMsg("WARNING: your last attempt to save your work was declined by the server.");
            alert("Request failed: server error.");
        }
    }
    return (
    <Fragment>
        {mainBtn}
        <br/>
        <p>{mainMsg}</p>
      <div className="detail-grid">
        <div className="span-2">
            <input value={movie.current.title} onChange={movieBinder} name="title" type="text" placeholder="Title" className="title-input" /><br/>
            <label>Runtime:</label>
            <input value={movie.current.runtime} onChange={movieBinder} name="runtime" type="text" /><br/>
            <label>Release date:</label>
            <input value={movie.current.releaseDate.toString()} onChange={movieBinder} name="releaseDate" type="date" />
            <textarea value={movie.current.summary} onChange={movieBinder} name="summary" placeholder="Write something about the story..." rows={20} />
        </div>
        <div>
            <img src={posterSource} onError={()=> { setPosterSource("/rewer/uploads/posters/0.jpg")}} alt="Poster" title="Poster" className="poster"/>
            {posterBottom}
        </div>
      </div>
    </Fragment>)
}

export default EditMovieDetails;