import * as caller from "./caller";
export interface User{
    userName: string;
    nickName: string;
}

export interface IDetailedUser extends User{
    about: string;
    permission: number;
    followerCount?: number;
    isFollowed?: number;
}

export async function selectDetailedUser(userName: string): Promise<IDetailedUser>{
    const response = await caller.POSTAsynch("/user/selectdetaileduser.php", { userName: userName });
    return caller.tryParse(response);
}

export async function updateUserInfo(user: IDetailedUser): Promise<boolean>{
    const response = await caller.POSTAsynch("/user/updateuserinfo.php", user);
    if(caller.isResponse200(response)){
        localStorage.setItem("nickName", user.nickName);
        return true;
    }
    return false;
}

export async function uploadAvatar(file: FormData){
    const response = await caller.UploadFile("/user/uploadavatar.php", file);
    try{
        if(JSON.parse(response).response === 200) return true;
        return false;
    } catch(err){
        console.log(err);
        console.log(response);
    }
    return false; 
}

export async function updatePermission(user: IDetailedUser): Promise<boolean>{
    const response = await caller.POSTAsynch("/user/updatepermission.php", user);
    return caller.isResponse200(response);
}

export async function doesExist(userName: string):Promise<boolean>{
    const data = {
        userName: userName
    }
    let response: string = await caller.POSTAsynch("/user/doesexist.php", data);
    try{
        return JSON.parse(response).exists;
    } catch(err) {
        console.log(err);
        console.log(response);
        return false;
    }
}

