import React, { useState, useEffect } from 'react'
import { deleteGenre, getGenres, IGenre, IMovie, IMovieGenre, insertGenre } from '../../objects/movie';
import { GenreSelect } from './GenreSelect';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes } from '@fortawesome/free-solid-svg-icons'
import { useAsyncReference } from '../../hooks';
interface IGenreAdder{
    movie: React.MutableRefObject<IMovie>;
    setMovieGenres: (newValue: IMovieGenre[])=> void;
}
export const GenreAdder: React.FC<IGenreAdder> = ({movie, setMovieGenres}: IGenreAdder)=> {
    const [genreList, setGenreList] = useState([]);
    const [newGenreId, setNewGenreId] = useAsyncReference(0);

    useEffect(()=>{
        requestGenres();
    },[]);

    function updateGenreId(value: string){
        setNewGenreId(parseInt(value));
    }

    async function requestGenres(){
        let genres = await getGenres();
        if(genres != null){
            setGenreList(genres);
        } else {
            alert("Server error: could not download the list of genres");
        }
    }

    async function addGenre(){
        if(newGenreId.current == null || newGenreId.current === 0){
            setNewGenreId(genreList[0].id);
        }
        let newGenre: IMovieGenre = {
            id: 0, 
            genreName: getGenreName(newGenreId.current),
            genreId: newGenreId.current,
            movieId: movie.current.id
        };
        newGenre = await insertGenre(newGenre);
        if(newGenre != null){
            setMovieGenres([...movie.current.genres, newGenre]);
        }
    }

    async function removeGenre(id: number){
        if(await deleteGenre(id)){
            setMovieGenres(movie.current.genres.filter(g=> g.id !== id));
        } else {
            alert("Server error");
        }
    }

    function getGenreName (id: number): string{
        return genreList.find(g=> g.id === id).name
    }

    return (
        <div>
            <h3>Genres:</h3>
            <div className= "edit-genre-container">
            {movie.current.genres.map(g=> {
                return <div key={`genre-${g.id}`} className="edit-genre"><span>{g.genreName}</span><button onClick={()=> { removeGenre(g.id); }} ><FontAwesomeIcon icon={faTimes} /></button></div>
            })}
            </div>          
            <GenreSelect genres={genreList} updateSelected={updateGenreId} /><button onClick={addGenre}>Add</button><br/>
        </div>
    )
}
export default GenreAdder;