import React, { useState, Fragment, useEffect } from 'react'
import { deleteFollow, IFollow, insertFollow } from '../../objects/follow';
import { IDetailedUser, updatePermission } from '../../objects/user'
import PermissionSelect from './PermissionSelect';
import "./profileStyle.css"

interface IUserInfo{
    user: IDetailedUser;
}

export const UserInfo: React.FC<IUserInfo> = ({user}: IUserInfo) => {
    const [getUser, setUser] = useState(user);
    const [followBtn, setFollowBtn] = useState(<Fragment></Fragment>);
    const [imgSrc, setImgSrc] = useState(`/rewer/uploads/avatars/${getUser.userName}.jpg`);
    const [follow, setFollow] = useState({id: getUser.isFollowed, follower: localStorage.getItem("userName"), followed: getUser.userName});
   
   async function updateUserPermission(newValue: string){
        const newPermission = {...getUser, permission: parseInt(newValue) }
        if(await updatePermission(newPermission)){
            setUser(newPermission);
        } else {
            alert("Server error");
        }  
        setUser( {...getUser, permission: parseInt(newValue)});
    } 

    useEffect(()=>{
        setUser(user);
    }, [user])

    useEffect(()=>{
        if(canBeFollowed(user)){
            placeFollowBtn();
        }
    }, [follow]);

    useEffect(()=>{
        setFollow({id: getUser.isFollowed, follower: localStorage.getItem("userName"), followed: getUser.userName});
    }, [getUser])

    function canBeFollowed(toFollow: IDetailedUser){
        return localStorage.getItem("userName") != null && localStorage.getItem("userName") !== toFollow.userName;
    }

    function placeFollowBtn(){
        if(follow.id > 0){
            setFollowBtn(<button onClick={unFollowUser} className="unfollow">Unfollow</button>);
        } else {
            setFollowBtn(<button onClick={followUser} className="follow">Follow</button>);
        }
    }

    async function followUser(){
        setFollowBtn(<button className="follow">Follow</button>); //prevent double clicking   
        let newId = await insertFollow(follow);
        if(newId){
            setFollow({...follow, id: newId});
        } else {
            alert("Server error.");
        }
    }

    async function unFollowUser(){
        setFollowBtn(<button className="unfollow">Unfollow</button>);
        if(await deleteFollow(follow)){
            setFollow({...follow, id: 0});
        } else {
            alert("Server error.");
        }
    }

    return (
        <div>
            <h1>{getUser.nickName}</h1>
            {followBtn}
            {parseInt(localStorage.getItem("permission")) === 3 ? 
                        (<Fragment>
                            <h3>User status:</h3>
                            <PermissionSelect defaultValue={getUser.permission.toString()} onUpdate={(newValue: string)=>{updateUserPermission(newValue)}} />
                        </Fragment>) 
                        : 
                        (<Fragment></Fragment>)}
            <div className="user-grid">
                <div>
                    <p>{getUser.about}</p>
                </div>
                <div>
                    <img src={imgSrc} onError={()=>{ setImgSrc(`/rewer/uploads/avatars/default.jpg`) }} alt="user-avatar" />
                </div>
            </div>
        </div>
    )
}

export default UserInfo;