import React, { Fragment, useEffect, useState, useRef } from "react";
import "../main.css";
import EditMovieDetails from "../components/movie/EditMovieDetails";
import { IMovieGenre, selectDetailedMovie } from "../objects/movie";
import { useAsyncBinder } from "../hooks";
import { getParameter } from "../urlManager";
import { EditCast, } from "../components/movie/EditCast";
import GenreAdder from "../components/movie/GenreAdder";

export default function EditMovie(){
    const [movie, setMovie, bindMovie] = useAsyncBinder({id: parseInt(getParameter("movieId")), title: "", releaseDate: new Date(), summary: "", runtime: 0, casts: [], genres: []});
    const dependentDiv = useRef<HTMLDivElement>();
    useEffect(()=>{
        if(movie.current.id > 0){
            getMovie();
        }
    }, []);
    async function getMovie(){
        let selectedMovie = await selectDetailedMovie(movie.current.id);
        if(selectedMovie != null){
            setMovie(selectedMovie);
            editModeOn();
        } else {
            alert("Server error");
        }
    }

    useEffect(()=>{
        if(movie.current.id > 0){
            editModeOn();
        } else {
            eidtModeOff();
        }
    }, [movie.current])

    function editModeOn(){
        dependentDiv.current.style.display = "block";
    }

    function eidtModeOff(){
        dependentDiv.current.style.display = "none";
    }

    function setMovieGenres(newGenres: IMovieGenre[]){
        setMovie({...movie.current, genres: newGenres});
    }
 
    return (
    <Fragment>
        <div className="main-container">
            <EditMovieDetails movie={movie} setMovie={setMovie} movieBinder={bindMovie} />
            <div ref={dependentDiv}>
                <GenreAdder movie={movie} setMovieGenres={setMovieGenres} />
                <EditCast movie={movie} setMovie={setMovie} />
            </div>
        </div>
    </Fragment>)
}